/* harmony default export */ var auto_complete = ({
    data: function data() {
        return {
            options: [],
            results: [],
            dataSource: [{ value: 'Burns Bay Road', label: 'Burns Bay Road' }, { value: 'Downing Street', label: 'Downing Street' }, { value: 'Wall Street', label: 'Wall Street' }]
        };
    },
    methods: {
        select: function select(value) {
            console.log('select', value);
        },
        search: function search(value) {
            this.options = !value ? [] : [{ value: value, label: value }, { value: value + value, label: value + value }, { value: value + value + value, label: value + value + value }];
        },
        handelSearch: function handelSearch(value) {
            if (!value || value.indexOf('@') >= 0) {
                this.results = [];
            } else {
                this.results = ['gmail.com', '163.com', 'qq.com'].map(function (domain) {
                    return { value: value + '@' + domain, label: value + '@' + domain };
                });
            }
        },
        filter: function filter(val, item) {
            var input = val.toLocaleUpperCase();
            return item.value.toUpperCase().indexOf(input) !== -1;
        }
    }
});
