<template><undefined>
<h1>AutoComplete 自动完成</h1>
<p>输入框自动完成功能。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>需要自动完成时。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div&gt;\n        &lt;v-auto-complete :data=\&quot;options\&quot; @select=\&quot;select\&quot; @search=\&quot;search\&quot; style=\&quot;width:200px;\&quot;&gt;&lt;/v-auto-complete&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data: ()=&gt; ({\n            options: [],\n        }),\n        methods: {\n            search(val) {\n                this.options = [{\n                    value: value,\n                    label: value\n                }, {\n                    value: value + value,\n                    label: value + value\n                }, {\n                    value: value + value + value,\n                    label: value + value + value\n                }];\n            },\n            select(value) {\n                console.log('select value', value)\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div>
        <v-auto-complete :data="options" @select="select" @search="search" style="width:200px;"></v-auto-complete>
    </div>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
