                  <div slot="description"><h4 id="bu-qu-fen-da-xiao-xie"><a class="header-anchor" href="#bu-qu-fen-da-xiao-xie" aria-hidden="true">¶</a> 不区分大小写</h4>
<p>不区分大小写的 AutoComplete</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-auto-complete</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"dataSource"</span> <span class="hljs-attr">:filter</span>=<span class="hljs-string">"filter"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:200px;"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-auto-complete</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
            <span class="hljs-attr">dataSource</span>: [
                { <span class="hljs-attr">value</span>: <span class="hljs-string">'Burns Bay Road'</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'Burns Bay Road'</span> },
                { <span class="hljs-attr">value</span>: <span class="hljs-string">'Downing Street'</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'Downing Street'</span> },
