<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="autocomplete-props"><a class="header-anchor" href="#autocomplete-props" aria-hidden="true">¶</a> AutoComplete Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定默认选中的条目</td>
<td>string/array</td>
<td>-</td>
</tr>
<tr>
<td>data</td>
<td>自动完成的数据源</td>
<td>array</td>
<td>[]</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入框提示</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>filter</td>
<td>搜索过滤函数,返回Boolean</td>
<td>搜索过滤函数,返回Boolean</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="data-props"><a class="header-anchor" href="#data-props" aria-hidden="true">¶</a> Data Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>选项的标签或组名(作为标签时可通过select的label属性修改；作为组名时只支持一级,可通过select的groupLabel属性修改)</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>options</td>
<td>分组的数据</td>
<td>array</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="autocomplete-events"><a class="header-anchor" href="#autocomplete-events" aria-hidden="true">¶</a> AutoComplete Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>search</td>
<td>搜索补全项的时候调用</td>
<td>value</td>
</tr>
<tr>
<td>select</td>
<td>被选中时调用，参数为选中项的 value 值</td>
<td>value</td>
</tr>
<tr>
<td>blur</td>
<td>blur事件</td>
<td>-</td>
</tr>
<tr>
<td>focus</td>
<td>focus事件</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
    export default {
        data: () => ({
            options: [],
            results: [],
            dataSource: [
                { value: 'Burns Bay Road', label: 'Burns Bay Road' },
                { value: 'Downing Street', label: 'Downing Street' },
                { value: 'Wall Street', label: 'Wall Street'},
            ],
        }),
        methods: {
            select(value) {
                console.log('select', value)
            },
            search(value) {
                this.options = !value ? [] : [
                    { value: value, label: value},
                    { value: value + value, label: value + value},
                    { value: value + value + value, label: value + value + value},
                ]
            },
            handelSearch(value) {
                if(!value || value.indexOf('@') >=0 ){
                    this.results = [];
                } else {
                    this.results = ['gmail.com', '163.com', 'qq.com'].map(domain => ({value: `${value}@${domain}`, label: `${value}@${domain}`}));
                }
            },
            filter(val, item){
                const input = val.toLocaleUpperCase();
                return item.value.toUpperCase().indexOf(input) !== -1;
            },
        }
    }
</script>


